.homeScreenBtn{
    margin-left: 15px;
    margin-right: 15px;
    /* background: yellowgreen; */
}
.homeScreenBtn__header{
    display: flex;
}
.homeScreenBtn__header > img {
    padding-right: 5px;
}
.homeScreenBtn__name{
    display: flex;
    justify-content: space-between;
    transform: translateY(-38px);
    margin-left: 20px;
    margin-right: 20px;
    /* background: violet; */

}
.homeScreenBtn__button{
    cursor: pointer;
background: #6C11CE;
transform: translateY(-60px);
height: 50px;
text-align: center;
margin-left: 15px;
margin-right: 15px;
border-radius: 15px;

}
.homeScreenBtn__button > h2{
    padding: 5px;
}