@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
.signUp{
    background: #080210;
    height: 100vh;

    
}
.signUp__form{
    width: 200px;
	height: 100px;
	position: absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
    color: white;
	margin: auto;

}
.textcolor{
    color: red !important;
}
.textLabel{
    font-family: 'Anton', sans-serif !important;
    color: white !important;
}
.beforeinput{
    color: red !important;
}
.MuiInput-underline:before{
    border-bottom: 2px solid #ffffff !important;
}
.MuiInput-underline:after{
    border-bottom: 2px solid #ff0000 !important;
}

.submit__button{
    /* background: #E5E5E5;
    color: #6C11CE;
    min-height: 40px;
    cursor: pointer; */
    margin-top: 10px;
    background: #6C11CE;
    color: #6C11CE;
    width: 200px;
    height: 55px;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: center;
    border: none;
    position: relative;
    text-decoration: none;
    cursor: pointer;
}
.submit__button >h4{
    /* padding: 15px; */
    margin-bottom: auto;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    text-decoration: none;
    color: #E5E5E5;
    cursor: pointer;
}