.store{
    background: #080210;
    height: 100%;
}
.store__header{
    display: flex;
    justify-content: space-between;
    margin-left: 15px;
    margin-right: 15px;
}
.header__left{
    display: flex;
}
.store__body{
    margin-left: 15px;
    margin-right: 15px;
}
.body__header{
    transform: translateY(-25px);
    height: 15px;
    margin-bottom: 0px;
}
.body__options{
    display: flex;
    justify-content: space-between;
}
.body__main{

}
.body__main > h2 {
    font-size: 48px;
}
.body__main >img{
   margin-top: 20px;
   
}
.powers{
    display: flex;
    justify-content: center;
}
.powers > img{
    padding-right: 5px;
}
.power__details{
    display: flex;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
}
.power__details >h2{
    color: #FF103A;
}
.purchase{
    margin-top: 50px;
}
.credits{
    transform: translateY(-30px);
    display: flex;
    justify-content: space-between  ;
}
.credits >h2{
    color: #18FF70;
}