@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
h2{
    color: #E5E5E5;
    font-family: 'Anton', sans-serif;
}
.home{
    background: #080210;
    height: 100vh;
}
.home__header{
    display: flex;
    justify-content: space-between;
}
.home__menu{
    padding-top:25px;
}
.home__store{
    display: flex;
}
.home__store > img{
    margin: 10px;
}

.home__credits{
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin: 15px;
}
.home__credits > h2 {
    color: #18FF70;
}

.home__courts{
    margin: 15px;
}
.tokens__owned{
    margin-right: 15px;
    margin-left: 15px;
}
.powers__home{
    margin-right: 15px;
    margin-left: 15px;
    display: flex;
    justify-content: flex-start;
    transform: translateY(-20px);
}
.details{
    margin-right: 15px;
    margin-left: 15px;
}
.splash{
    display: block;
}
.splash.display-none{
    display: none;
}