.scoreBoard{
    background: #080210;
    /* height: 100vh;  */
}

.scoreBoard__header{
    display: flex;
    margin-left: 15px;
    margin-right: 15px;
    justify-content: space-between;

}

.board{
    margin-left: 15px;
    margin-right: 15px;
    border: 2px solid #6C11CE;
    box-shadow: white;
    box-shadow: 5px 10px #6C11CE;
    height: 255px;
    
}

.board__timer{
    background: #18FF70;
    height: 30px;
    width: 80px;
    border-radius: 50px;
    text-align: center;
    padding-top: 15px;
    color: white;
    transform: translateY(-20px);
}
.board__score{
    display: flex;
    justify-content: space-between;
    transform: translateY(-40px);
}

.board__light{
    display: flex;
    justify-content: space-between;
    transform: translateY(-40px);
}
.board__lead{
    transform: translateY(-50px);
}
.board__light__left,.board__light__right{
    display: flex;
}

.green,.red,.white{
    
    height: 10px;
    width: 20px;
    border-radius: 50px;
    margin-right: 5px;
}
.board__player{
    display: flex;
    justify-content: space-evenly;
    opacity: 50%;
    transform: translateY(-90px);
}.topThree__header{
    display: flex;
    justify-content: space-between;
}
.left{
    display: flex;
}