.joincourt{
    background: #080210;
    height: 100vh;
}
.joincourt__body{
    display: flex;
    flex-direction: column;
    margin-left: 15px;
    margin-right: 15px;

}
.headerleft{
    display: flex;
}
.headerleft > img{
    padding-right: 5px;;
}
.joincourt__header{
    display: flex;
    justify-content: space-between;
    margin-left: 15px;
    margin-right: 15px;
}

.search__header{
    display: flex;
}
.search__header > img{
    padding-right: 5px;
}

/* input */
.textcolor{
    color: red !important;
}
.textLabel{
    font-family: 'Anton', sans-serif !important;
    color: white !important;
}
.beforeinput{
    color: red !important;
}
.search__input{
    background: #291D39;
    height: 80px;
    margin: 15px;
    border-radius: 20px;
    padding-top:5px ;
    align-items: center;
}
.search__button{
    margin-top: 90px;
}

.open__header{
    display: flex;
    
}
.open__header >img {
    padding-right: 5px;
}

.createCourt__button{
    
    margin-top: 80px;
}